<template lang="html">
  <div class="szmx">
    <div class="szmx-wrapper">
      <div class="szmx-wrapper-top">
        <div class="szmx-wrapper-top-l">
          <span>收支明细</span>
          <span>/</span>
          <span>说明说明说明说明说明说明说明说明</span>
        </div>
        <div class="szmx-wrapper-top-r">
          <div class="szmx-wrapper-top-r-l">
            EXCEL导出
          </div>
          <router-link to="/YFZH" tag="div" class="szmx-wrapper-top-r-r">返回上一页
          <i class="el-icon-arrow-right"></i></router-link>
        </div>

      </div>
      <div class="szmx-wrapper-bottom">
        <div class="szmx-wrapper-bottom-wrapper">
          <div class="szmx-wrapper-bottom-t">
            <div class="szmx-wrapper-bottom-t1">
              <div class="hm">
                <p>对方户名：</p>
                <p style="width:226px;">
                  <el-input size="large" placeholder="姓名、车牌号、手机号" v-model="input6"></el-input>
                </p>
              </div>
              <div class="hm">
                <p>对方账号：</p>
                <p style="width:226px;">
                  <el-input size="large" placeholder="选择" v-model="input6"></el-input>
                </p>
              </div>
              <div class="hm">
                <p>时间：</p>
                <p style="width:226px;">
                  <el-time-select v-model="value1" :picker-options="{start: '08:30',step: '00:15',end: '18:30'}" placeholder="选择区间"></el-time-select>
                </p>
              </div>
              <div class="hm">
                <p>借贷类型：</p>
                <p style="width:226px;">
                  <el-select v-model="value" placeholder="选择区间">
                    <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value"></el-option>
                  </el-select>
                </p>
              </div>
            </div>
            <div class="szmx-wrapper-bottom-t2">
              <div class="hm">
                <p>业务类型：</p>
                <p style="width:226px;">
                  <el-select v-model="value" placeholder="选择区间">
                    <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value"></el-option>
                  </el-select>
                </p>
              </div>
              <div class="hm cx"  style="width:226px;">
                <el-button type="primary" @click="onSubmit"  style="width:226px;backgorund:#3052ac;">查询</el-button>
              </div>
            </div>

          </div>
          <div class="szmx-wrapper-bottom-b">
            <el-table :data="tableData4" border style="width: 100%" max-height="250">
              <el-table-column prop="date" label="日期" width="185"></el-table-column>
              <el-table-column prop="zh" label="账户"></el-table-column>
              <el-table-column prop="sr" label="收入"></el-table-column>
              <el-table-column prop="zc" label="支出"></el-table-column>
              <el-table-column prop="dfzh" label="对方账户"></el-table-column>
              <el-table-column prop="czr" label="操作人"></el-table-column>
              <el-table-column prop="glzfd" label="关联支付单"></el-table-column>
              <el-table-column prop="ywlx" label="业务类型"></el-table-column>
              <el-table-column label="操作">
                <template scope="scope">
                  <el-button @click.native.prevent="deleteRow(scope.$index, tableData4)" type="text" size="small">
                    查看交易明细
                  </el-button>
                </template>
              </el-table-column>
            </el-table>
          </div>


        </div>

      </div>
    </div>
  </div>
</template>

<script>
export default {
  data(){
    return{
      options: [{
         value: '选项1',
         label: '黄金糕'
       }, {
         value: '选项2',
         label: '双皮奶'
       }, {
         value: '选项3',
         label: '蚵仔煎'
       }, {
         value: '选项4',
         label: '龙须面'
       }, {
         value: '选项5',
         label: '北京烤鸭'
       }],
       value: '',
       value1: '',
       tableData4: [{
          date: '2016-09-23 08:52:20',
          zh: '',
          sr: '',
          zc: '8796.00',
          dfhm: '四川通胜钻探有限公司',
          dfzh: '30200249000999',
          czr:'张士豪',
          glzfd:'1312321',
          ywlx:'服务费结算'
        },
        {
           date: '2016-09-23 08:52:20',
           zh: '',
           sr: '1980.00',
           zc: '',
           dfhm: '温朵',
           dfzh: '6228480402564890018',
           czr:'张士豪',
           glzfd:'1312321',
           ywlx:'运费结算'
         },
         {
            date: '2016-09-23 08:52:20',
            zh: '',
            sr: '',
            zc: '287961.00',
            dfhm: '新疆三农投资有限公司',
            dfzh: '512060100100133675',
            czr:'张士豪',
            glzfd:'1312321',
            ywlx:'提现'
          },
          {
             date: '2016-09-23 08:52:20',
             zh: '',
             sr: '1980.00',
             zc: '8796.00',
             dfhm: '合肥维天运通信系科技股份有限公司',
             dfzh: '30200249000551',
             czr:'张士豪',
             glzfd:'1312321',
             ywlx:'归还保理'
           }]
    }
  }
}
</script>

<style lang="less" scoped>
.szmx{
  background-color: #FAFBFF;
  border-top:1px solid #e6e9f2;
  .szmx-wrapper{
    width: 1540px;
    margin: 0 auto;
    .szmx-wrapper-top{
      width: 1540px;
      display: flex;
      justify-content: space-between;
      height: 90px;
      line-height: 90px;
      .szmx-wrapper-top-l{
        span:nth-of-type(1){
          font-size: 20px;
          color: #3257a7;
          font-weight: 700;
        }
        span:nth-of-type(2){
          margin: 0 5px;
        }
        span{
          font-size: 16px;
          color: #9ba5be;
        }
      }
      .szmx-wrapper-top-r{
        display: flex;
        justify-content: space-around;
        align-items: center;
        .szmx-wrapper-top-r-l{
          width: 100px;
          height: 40px;
          color: #54885a;
          font-size: 12px;
          text-align: center;
          line-height: 40px;
          border: 1px solid #54885a;
          margin-right: 20px;
        }

        .szmx-wrapper-top-r-r{
          font-size: 12px;
          color: #a5a4a9;
        }

      }
    }
    .szmx-wrapper-bottom{
      background-color:#fff;
      border:1px solid #e6e9f2;
      .szmx-wrapper-bottom-wrapper{
        width: 1482px;
        margin: 0 auto;
        .szmx-wrapper-bottom-t{
          border-bottom: 2px solid #e6e9f2;
          padding:40px 0;
          .szmx-wrapper-bottom-t1,.szmx-wrapper-bottom-t2{
            display: flex;
            align-items: center;
            .hm:nth-of-type(3){
              width: 280px
            }
            .hm{
              width:318px;
              display: flex;
              align-items: center;
              p:nth-of-type(1){
                color: #7d9db6;
                font-size: 16px;
                font-weight: 700;
              }
              p{
                input{
                  background-color: #f6f5fa;
                }
              }
            }
            .cx{
              margin-left: 75px;
            }
          }
          .szmx-wrapper-bottom-t1{
            margin-bottom: 20px;
          }
        }
        .szmx-wrapper-bottom-b{
          padding: 30px 0;
        }
      }
    }
  }
}
</style>



